import React,{Component} from "react";
import { Form, Table } from "react-bootstrap";
import { AddRecipe } from "./AddRecipe";
import {Button, ButtonToolbar} from 'react-bootstrap';
import Axios from 'axios';
import RecipeTableRow from "./RecipeTableRow";



export class Myrecipes extends Component{

    constructor(props){
        super(props);
        this.state= {
            recipes:[]
        }
        this.deleteRecipe = this.deleteRecipe.bind(this);
    }

     config = {
        headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            "Access-Control-Allow-Origin" : "",
            "Allow": "GET",
            "Content-type": "Application/json",
        }
    };
    
    
    
    componentDidMount(){
        const _this = this;

        Axios.get('/Recipe').then(function (response) {
            _this.setState({
                recipes: response.data
             })
        }) 

    }
    
    deleteRecipe(id){
        let newRecipes = this.state.recipes.filter(function(recipe){
            return recipe.id !== id
        });
 
        this.setState({
            recipes: newRecipes
        });
    }


    render(){
        const _this = this;
        return(
            <div>
                <Table className="mt-0" striped bordered hover size="sm">
                <thead>
                    <tr>
                    <th>Recipe Name</th>
                    <th>Category</th>
                    <th>Options</th>
                    </tr>
                </thead>
                <tbody>
                    {this.state.recipes.map(function(recipe){
                        return <RecipeTableRow recipe={recipe} onDelete={_this.deleteRecipe} />
                    })}
                </tbody>
                </Table>
                <AddRecipe/>
            </div>
        )
    }
}